import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {
    HashRouter
} from 'react-router-dom';
import {
    Routes,
    Route
} from 'react-router';
import Home from './containers/Home/home';
import AboutMe from "./containers/AboutMe/AboutMe";
import Works from "./containers/Works/works";
import Biography from "./containers/Biography/biography";
import Skills from "./containers/Skills/skills";
import Timeline from "./containers/Timeline/timeline";

ReactDOM.render(
    <HashRouter basename='/website'>
        <Routes>
            <Route path='/website' element={<Home/>}/>
            <Route path='/website/Home' element={<Home/>}/>
            <Route path='/website/About-Me' element={<AboutMe/>}/>
            <Route path='/website/Biography' element={<Biography/>}/>
            <Route path='/website/Skills' element={<Skills/>}/>
            <Route path='/website/Timeline' element={<Timeline/>}/>
            <Route path='/website/Works' element={<Works/>}/>
        </Routes>
    </HashRouter>,
    document.getElementById('root')
);
